.App {
  text-align: center;
}

.App-logo {
  height: 30vmin;
  pointer-events: none;
  background: var(--color-button-background-primary-press, #e61717);
  opacity: 0.4;
  border-radius: var(--border-radius-4, 4px);
}

@media (prefers-reduced-motion: no-preference) {
  .App-logo {
    animation: App-logo-spin infinite 20s linear;
  }
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: var(--complementary-gold-medium, #b79967);
  background: var(--grey-900, #222);
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
